export default "<template>\n  <bean-table\n    :columns=\"columns\"\n    :value=\"value\"\n    :actions=\"actions\"\n    :actionColumnProps=\"actionColumnProps\"\n    @cell-click=\"handleCellClick\"\n  />\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\n\n@Component\nexport default class Example extends Vue {\n  value = [\n    {\n      id: 1,\n      province: '北京市',\n      city: '北京市',\n      district: '东城区',\n      created_at: '2021-11-17 11:33:04',\n      updated_at: '2021-11-17 11:33:04',\n    },\n    {\n      id: 2,\n      province: '广东省',\n      city: '广州市',\n      district: '海珠区',\n      created_at: '2021-11-17 11:33:04',\n      updated_at: '2021-11-17 11:33:04'\n    }\n  ];\n\n  columns = [\n    // 其他prop 参考 https://element.eleme.cn/#/zh-CN/component/table#table-column-attributes\n    {\n      prop: 'province',\n      label: '省',\n    },\n    {\n      prop: 'city',\n      label: '市',\n    },\n    {\n      prop: 'district',\n      label: '区'\n    },\n    {\n      prop: 'created_at',\n      label: '创建于',\n      renderCell: 'time',\n      minWidth: 100,\n    },\n    {\n      prop: 'updated_at',\n      label: '更新于',\n      minWidth: 150,\n      renderCell: (h, { props: { value } }) => {\n        return <span>时间:{value}</span>\n      }\n    }\n  ]\n\n  actionColumnProps = {\n    label: '操作列',\n    // 其他prop 参考 https://element.eleme.cn/#/zh-CN/component/table#table-column-attributes\n  }\n\n  actions(scope) {\n    return [\n      {\n        component: 'confirmButton',\n        props: {\n          label: `删除${scope.row.province}`,\n          type: 'danger',\n        },\n        on: {\n          confirm: () => {\n            alert('删除');\n          }\n        }\n      },\n      {\n        component: () => <el-button type=\"warning\" onClick={() => alert('其他操作')}>其他操作</el-button>\n      }\n    ]\n  }\n\n  handleCellClick() {\n    alert('cell click');\n  }\n\n}\n</script>\n";