export default "<template>\n  <bean-source-page\n    resource=\"topics\"\n    type=\"show\"\n    title=\"Topics\"\n    :tabs=\"tabs\"\n    :onFetchData=\"onFetchData\"\n    :actions=\"actions\"\n  />\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\nimport _ from 'lodash';\n\nconst TABS = {\n  ask: '问答',\n  share: '分享',\n  job: '招聘',\n  good: '精华'\n}\n\n@Component\nexport default class TopicsDetail extends Vue {\n\n  async onFetchData() {\n    const baseURL = 'https://cnodejs.org/api/v1';\n    const { data } = await this.$request.get(`/topic/${this.$route.params.id}`, {}, { baseURL });\n    return { data };\n  }\n\n  tabs = [\n    {\n      label: '基本信息',\n      columns: [\n      {\n        prop: 'id',\n        label: 'ID',\n        minWidth: 120\n      },\n      {\n        prop: 'title',\n        label: '标题',\n        minWidth: 150\n      },\n      {\n        prop: 'tab',\n        label: '主题分类',\n        minWidth: 120,\n        renderCell: (h, { props: { value } }) => {\n          return <el-tag>{ TABS[value] }</el-tag>\n        }\n      },\n      {\n        prop: 'visit_count',\n        label: '访问量',\n        minWidth: 100\n      },\n      {\n        prop: 'reply_count',\n        label: '回复数',\n        minWidth: 100\n      },\n      {\n        prop: 'author.avatar_url',\n        label: '作者头像',\n        renderCell: (h, { props: { value } }) => {\n          return <el-image src={value} />\n        },\n        minWidth: 100\n      },\n      {\n        prop: 'author',\n        label: '作者名称',\n        minWidth: 120,\n        renderCell: (h, { props: { scope } }) => {\n          return <span>{_.get(scope.row, 'author.loginname')}</span>\n        },\n      },\n      {\n        prop: 'good',\n        label: '精华',\n        renderCell: 'bool',\n        minWidth: 80\n      },\n      {\n        prop: 'top',\n        label: '置顶',\n        renderCell: 'bool',\n        minWidth: 80\n      },\n      {\n        prop: 'create_at',\n        label: '创建于',\n        renderCell: 'time',\n        minWidth: 150\n      },\n      {\n        prop: 'last_reply_at',\n        label: '最后评论于',\n        renderCell: 'time',\n        minWidth: 150\n      },\n      ]\n    },\n    {\n      label: 'tab1',\n      renderCell: (h, value) => {\n        return <pre>{JSON.stringify(value, null, 2)}</pre>\n      }\n    }\n  ];\n\n  actions() {\n    return [];\n  }\n}\n\nexport const route = [\n  {\n    path: '/topics-tab/:id',\n    name: 'topics-tab.show',\n    component: TopicsDetail\n  },\n]\n</script>\n\n";