export default "<template>\n  <div>\n    <bean-column-render :renderCell=\"renderCellHtmlTag\" />\n    <br>\n    <bean-column-render :renderCell=\"renderCellElementTag\" />\n  </div>\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\n@Component\nexport default class ColumnRenderTag extends Vue {\n  renderCellHtmlTag = {\n    component: 'div',\n    class: 'div',\n    style: {\n      width: '100px',\n      height: '100px',\n      backgroundColor: 'red',\n      color: '#fff',\n      textAlign: 'center'\n    },\n    domProps: {\n      innerHTML: '<span>div</span>'\n    },\n    on: {\n      click() {\n        alert('click');\n      }\n    },\n    ref: 'div'\n  }\n\n  // renderCellHtmlTag = 'button'; // 不需要设置其他属性时可以直接填组件名称\n\n  renderCellElementTag = {\n    component: 'el-button',\n    props: {\n      type: 'primary',\n      size: 'small'\n    },\n    domProps: {\n      innerText: '按钮'\n    },\n    on: {\n      click() {\n        alert('click');\n      }\n    },\n    nativeOn: {\n      click() {\n        alert('nativeOn click');\n      }\n    },\n    ref: 'elementButton'\n  }\n\n  mounted() {\n    console.log(this.$refs.div);\n    console.log(this.$refs.elementButton);\n  }\n}\n</script>\n";