export default "<template>\n  <div>\n    <bean-form\n      :columns=\"columns\"\n      v-model=\"form\"\n      @submit=\"handleSubmit\"\n    />\n    <hr>\n    <bean-form\n      :columns=\"columns\"\n      v-model=\"form2\"\n      @submit=\"handleSubmit2\"\n    >\n      <template #header>\n        <div>表单header</div>\n      </template>\n      <template #action=\"{ submit, clear, reset }\">\n        <el-button @click=\"submit\" type=\"danger\">保存</el-button>\n        <el-button @click=\"clear\" type=\"warning\">清空</el-button>\n        <el-button @click=\"reset\" type=\"info\">重置</el-button>\n      </template>\n    </bean-form>\n  </div>\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\n@Component\nexport default class Example extends Vue {\n\n  form = {};\n\n  form2 = {\n    title: '默认标题'\n  };\n\n  columns = [\n    {\n      prop: 'title',\n      label: '标题',\n      renderCell: {\n        required: true,\n        component: 'input'\n      }\n    },\n    {\n      prop: 'number',\n      label: '数字',\n      renderCell: {\n        required: true,\n        component: 'input',\n        hint: '请输入数字',\n        attrs: {\n          placeholder: '请输入数字'\n        },\n        rules: [\n          {\n            type: 'string',\n            pattern: /^\\d+$/,\n            message: '请输入数字'\n          }\n        ]\n      }\n    }\n  ];\n\n  handleSubmit(data) {\n    alert(JSON.stringify(data, null, 2));\n  }\n\n  handleSubmit2(data) {\n    alert(JSON.stringify(data, null, 2));\n  }\n\n}\n</script>\n";