export default "<template>\n  <bean-source-page\n    resource=\"topics\"\n    :type=\"type\"\n    title=\"Topics\"\n    :columns=\"columns\"\n    :filterColumns=\"filterColumns\"\n    :scopeColumns=\"scopeColumns\"\n    :formColumns=\"formColumns\"\n    :onFetchData=\"onFetchData\"\n    :actions=\"actions\"\n    :batchActions=\"batchActions\"\n    :exportProps=\"exportProps\"\n    :importProps=\"importProps\"\n    enableTableToExcel\n  />\n</template>\n\n<script>\nimport { Vue, Component, Prop } from 'vue-property-decorator';\nimport _ from 'lodash';\n\nconst TABS = {\n  ask: '问答',\n  share: '分享',\n  job: '招聘',\n  good: '精华'\n}\n\n@Component\nexport default class Topics extends Vue {\n  @Prop(String) type;\n\n  form = {};\n\n  filterColumns = [\n    {\n      prop: 'tab',\n      label: '主题分类',\n      renderCell: {\n        component: 'select',\n        props: {\n          options: _.map(TABS, (label, value) => ({ label, value })),\n          clearable: true\n        }\n      }\n    }\n  ]\n\n  scopeColumns = [\n    {\n      prop: 'tab',\n      label: '主题分类',\n      renderCell: {\n        component: 'radioGroup',\n        props: {\n          options: _.map(TABS, (label, value) => ({ label, value }))\n        }\n      }\n    }\n  ]\n\n  columns = [\n    {\n      prop: 'id',\n      label: 'ID',\n      minWidth: 120\n    },\n    {\n      prop: 'title',\n      label: '标题',\n      minWidth: 150\n    },\n    {\n      prop: 'tab',\n      label: '主题分类',\n      minWidth: 120,\n      renderCell: (h, { props: { value } }) => {\n        return <el-tag>{ TABS[value] }</el-tag>\n      }\n    },\n    {\n      prop: 'visit_count',\n      label: '访问量',\n      minWidth: 100\n    },\n    {\n      prop: 'reply_count',\n      label: '回复数',\n      minWidth: 100\n    },\n    {\n      prop: 'author.avatar_url',\n      label: '作者头像',\n      renderCell: (h, { props: { value } }) => {\n        return <el-image src={value} />\n      },\n      minWidth: 100\n    },\n    {\n      prop: 'author',\n      label: '作者名称',\n      minWidth: 120,\n      renderCell: (h, { props: { scope } }) => {\n        return <span>{_.get(scope.row, 'author.loginname')}</span>\n      },\n    },\n    {\n      prop: 'good',\n      label: '精华',\n      renderCell: 'bool',\n      minWidth: 80\n    },\n    {\n      prop: 'top',\n      label: '置顶',\n      renderCell: 'bool',\n      minWidth: 80\n    },\n    {\n      prop: 'create_at',\n      label: '创建于',\n      renderCell: 'time',\n      minWidth: 150\n    },\n    {\n      prop: 'last_reply_at',\n      label: '最后评论于',\n      renderCell: 'time',\n      minWidth: 150\n    },\n  ];\n\n  formColumns = [\n    {\n      prop: 'title',\n      label: '标题',\n      renderCell: {\n        required: true,\n        component: 'input',\n        rules: [\n          {\n            type: 'string',\n            min: 4,\n            max: 20,\n            message: '长度4 - 20'\n          },\n          // {\n          //   type: 'string',\n          //   pattern: /^[A-Za-z]+$/,\n          //   message: '只能填写英文大小写字母'\n          // }\n        ]\n      }\n    },\n    {\n      prop: 'tab',\n      label: '主题分类',\n      renderCell: {\n        component: 'select',\n        required: true,\n        props: {\n          options: _.map(TABS, (label, value) => ({ label, value })),\n        }\n      }\n    },\n    {\n      prop: 'content',\n      label: '内容',\n      renderCell: 'editor'\n    }\n  ]\n\n  exportProps = {\n    buttonText: '导出主题',\n    url: '/export'\n  }\n\n  importProps = {\n    buttonText: '导入主题',\n    url: '/import'\n  }\n\n  async onFetchData({ type }) {\n    const limit = this.$route.query.per_page || 10;\n    const page = this.$route.query.page || 1;\n    const baseURL = 'https://cnodejs.org/api/v1';\n    if (type === 'index') {\n      const { data } = await this.$request.get('/topics', { ...this.$route.query, limit, page }, { baseURL });\n      return {\n        data,\n        pagination: {\n          total: 100,\n          pageSize: limit,\n          currentPage: page\n        }\n      }\n    }\n    if (this.type === 'edit' || this.type === 'show') {\n      const { data } = await this.$request.get(`/topic/${this.$route.params.id}`, {}, { baseURL });\n      return { data };\n    }\n    if (this.type === 'new') {\n      return {\n        data: this.form\n      }\n    }\n\n  }\n\n  batchActions({ selection }) {\n    return [\n      {\n        component: () => <el-button onClick={() => alert('批量操作')} disabled={ selection.length === 0 }>批量操作</el-button>\n      }\n    ]\n  }\n\n  actions({ row }) {\n    return [\n      {\n        component: 'routeButton',\n        props: {\n          to: { name: 'topics.edit', params: { id: row.id } },\n          type: 'primary',\n          label: '编辑'\n        }\n      },\n      {\n        component: 'confirmButton',\n        props: {\n          label: '删除',\n          type: 'danger',\n        },\n        on: {\n          confirm: () => {\n            alert('删除');\n          }\n        }\n      },\n      {\n        component: () => <el-button type=\"warning\" onClick={() => alert('其他操作')}>其他操作</el-button>\n      }\n    ];\n  }\n}\n\nexport const route = [\n  {\n    path: '/topics',\n    name: 'topics.index',\n    component: Topics,\n    props: {\n      type: 'index',\n    }\n  },\n  {\n    path: '/topics/new',\n    name: 'topics.new',\n    component: Topics,\n    props: {\n      type: 'new',\n    }\n  },\n  {\n    path: '/topics/:id',\n    name: 'topics.show',\n    component: Topics,\n    props: {\n      type: 'show',\n    }\n  },\n  {\n    path: '/topics/:id/edit',\n    name: 'topics.edit',\n    component: Topics,\n    props: {\n      type: 'edit'\n    }\n  }\n];\n</script>\n";