export default "<template>\n  <bean-source-page\n    resource=\"topics\"\n    type=\"new\"\n    title=\"Topics\"\n    :formColumns=\"formColumns\"\n    :onFormSubmit=\"onFormSubmit\"\n  />\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\nimport _ from 'lodash';\n\nconst TABS = {\n  ask: '问答',\n  share: '分享',\n  job: '招聘',\n  good: '精华'\n}\n\nconst CustomInput = {\n  template: `\n    <div>\n      <p>CustomInput</p>\n      <el-input :value=\"value\" @input=\"$emit('change', $event)\" />\n    </div>\n  `,\n  props: {\n    value: {\n      type: String,\n      default: ''\n    }\n  },\n}\n\n@Component\nexport default class TopicsForm extends Vue {\n\n  formColumns(h, { data, change }) {\n    return [\n      {\n        prop: 'title',\n        label: '标题',\n        renderCell: {\n          component: 'input',\n          required: true\n        }\n      },\n      {\n        prop: 'tab',\n        label: '主题分类',\n        minWidth: 120,\n        renderCell: {\n          component: 'select',\n          required: true,\n          props: {\n            options: _.map(TABS, (label, value) => ({\n              label,\n              value\n            }))\n          },\n          on: {\n            change(value) {\n              // 修改title字段\n              change({ title: `[${value}]${data.title || ''}` })\n            }\n          }\n        }\n      },\n      {\n        prop: 'customInput',\n        label: '自定义组件',\n        renderCell: {\n          // 组件接收value props, 有一个change事件\n          component: (h, context) => <CustomInput {...context} />\n        }\n      }\n    ]\n  }\n\n  onFormSubmit({ data }) {\n    alert(JSON.stringify(data, null, 2));\n  }\n}\n\nexport const route = [\n  {\n    path: '/topics-form',\n    name: 'topics-form',\n    component: TopicsForm\n  },\n]\n</script>\n\n";